.product {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  height: 100%;
  background-color: #d9d9d900;
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;


  &:focus {
    outline: 2px dashed #fff;
    outline-offset: 3px;
  }

  @include hover {
    box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.3);
    transform: scale(105%);
  }

  &__img {
    height: 200px;
    max-width: 300px;

    img {
      width: 300px;
      height: 200px;
    }
  }


  &__bottom {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding: 20px;
    width: 100%;
    min-height: 250px;
    gap: 1.25rem;
  }

  &__title {
    display: block;
    margin-bottom: 1rem;
    font-weight: 600;
    font-size: 1.7rem;
    line-height: 27px;
    text-align: center;
    color: rgb(0, 0, 0);
    user-select: none;
  }


  &__price {
    margin-bottom: 1rem;
    font-weight: 800;
    font-size: 2.25rem;
    color: rgb(255, 51, 0);
    user-select: none;
  }

  &__btn {
    margin-top: auto;
    width: 80%;

    &:focus {
      outline: 2px dashed #000;
      outline-offset: 3px;
    }

    &:disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

}
