.btn {
  border-radius: 5px;
  padding: 1rem 1.25rem;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #000;
  background-color: rgb(246, 167, 0);
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;


  &:focus {
    outline: 2px dashed #fff;
    outline-offset: 3px;
  }

  @include hover {
    background-color: rgb(246, 98, 0);
  }

  &:active {
    color: #fff;
    background-color: rgb(2, 15, 201);
  }

}
