.cart {
  position: relative;

  &__quantity {
    position: absolute;
    left: 20%;
    top: -5%;
    transform: translateY(-50%);
    background-color: rgb(246, 167, 0);
    border-radius: 100%;
    color: #000;
    padding: 5px;
    width: 20px;
    height: 20px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 0;
  }

  &-content {
    position: absolute;
    right: 0;
    top: 100%;
    width: 45vw;
    background-color: #000000f5;
    border-radius: 10px;
    box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.3);
    opacity: 0;
    transition: opacity 0.3s, visibility 0.3s;


    @include small-tablet {
      width: 95vw;
    }

    &__list {
      max-height: 300px;
      overflow-y: auto;
    }

    &__product {
      border-bottom: 1px solid #F6A700;
    }

    &__bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 20px;
    }

    &__fullprice {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;
      color: rgb(255, 255, 255);
      font-size: 16px;
      font-weight: 500;
      line-height: 19px;
      text-align: left;
    }
  }

  &-product {
    display: flex;
    align-items: center;
    padding: 10px 20px;

    &__img {
      max-width: 100px;
      max-height: 100px;
      margin-right: 20px;
    }

    &__title {
      max-width: 280px;
      font-weight: 600;
      font-size: 1.13rem;
      line-height: 1.61rem;
      text-align: left;
      color: rgb(255, 255, 255);
      margin-bottom: 1rem;
    }

    &__delete {
      width: 20px;
      height: 20px;
      margin-left: auto;
      margin-right: 20px;
      background-image: url("../img/trash.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-color: transparent;
    }
  }

}

.cart.active:hover .cart-content {

  opacity: 1;
  visibility: visible;
}

.simplebar-track .simplebar-vertical .simplebar-scrollbar::before {
  background: rgba(77, 77, 77, 0.2);
  border-radius: 5px;
  width: 100%;
  opacity: 1;
}
