.stepper {
  position: relative;
  display: flex;
  flex: 0 0 40%;
  align-items: center;
  height: 50px;
  user-select: none;

  &__field {
    display: inline-flex;
    align-items: center;
    overflow: hidden;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    padding-left: 20px;
    width: 100%;
    height: 100%;
  }

  &__input {
    outline: none;
    border: none;
    padding-right: 5px;
    width: 10px;
    height: 100%;
    min-width: 2ex;
    font-weight: 700;
  }

  &__text {
    font-weight: 700;
  }

  &__btns {
    position: absolute;
    right: 2px;
    top: 50%;
    display: flex;
    flex-direction: column;
    border-left: #e5e5e5 1px solid;
    width: 20%;
    height: calc(100% - 2px);
    transform: translateY(-50%);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    padding: 0;
    height: 50%;
    background-color: transparent;
    cursor: pointer;
  }

  &__btn--disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}
