.products {
  &__title {
    display: inline-block;
    margin-bottom: 35px;
    font-weight: 700;
    font-size: 36px;
    line-height: 45px;
    text-align: left;
    color: rgb(0, 0, 0);

    &::after {
      content: '';
      display: inline-block;
      width: 80%;
      height: 4px;
      background-color: rgb(246, 167, 0);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    margin-bottom: 35px;
    gap: 20px;


    @include tablet {
      grid-template-columns: repeat(9, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__item {
    grid-column: 3 span;
  }

  &__link {
    transition: all 0.3s ease-in-out;
    user-select: none;

    &:focus {
      outline: 2px dashed #000;
      outline-offset: 3px;
    }

    @include hover {
      color: rgb(246, 98, 0);
    }

    &:active {
      color: rgb(255, 1, 1);
    }
  }
}
