.hero {
  height: 100vh;
  min-height: 700px;
  background-image: url("../img/hero_bgrnd.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;


@include small-tablet {
  width: 100%;
  background-image: url("../img/hero-bgrd-360.jpeg");
}

  &__container {
    position: relative;
    height: 100%;


    @include tablet {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__wrapper {
    position: absolute;
    top: 28%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding-left: 15px;
    max-width: 550px;
    text-align: left;
    color: rgb(255, 255, 255);
    gap: 23px;

    @include tablet {
      align-items: center;
      justify-content: space-between;
      height: 30%;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 50px;
    line-height: 45px;

    @include tablet {
      margin-bottom: auto;
      text-align: center;
    }

    @include mobile {
      font-size: 36px;
    }
  }

  &__descr {
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;

    @include mobile {
      display: none;
    }
  }

}
